// `SDK` , , 
const onDisconnect = (error: any) => {
  console.log('' + error.message + '' + error.code);
  ImModule.Disconnect(false);//contacting
  if (error) {
    switch (error.code) {
      // , 
      case 302:
        Message({
          message: error.message,
          type: 'warning'
        });
        break;
      // , , 
      case 417:
        break;
      // , 
      case 'kicked':
        break;
      default:
        break;
    }
  }
}

