export const initNimSDK = () => {
  if (ImModule.nim) {
    (ImModule.nim as any).disconnect()
  }
  const loginInfo = {
    uid: cookie.readCookie('uid'),
    sdktoken: cookie.readCookie('sdktoken'),
  }
  nim = SDK.NIM.getInstance({
    // debug: true,
    isHistoryable: true,//
    isRoamingable: false,//
    isOfflinable: true,// 
    isUnreadable: true,//
    isSyncable: true,//

    appKey: process.env.VUE_APP_IM_APPKEY,
    account: cookie.readCookie('uid'),
    token: cookie.readCookie('sdktoken'),
    // transports: ['websocket'],
    // db: false,
    // syncSessionUnread: true,
    // autoMarkRead: true, // true
    onconnect: onConnect,
    onerror: onError,
    onwillreconnect: onWillReconnect,
    ondisconnect: onDisconnect,//
    onloginportschange: onLoginPortsChange,
    onmyinfo: onMyInfo,
    onusers: onUsers,
    onupdateuser: onUpdateUser,
    onsessions: onSessions,
    onupdatesession: onUpdateSession,
    onofflinemsgs: onOfflineMsgs,
    onmsg: onMsg
  });
  (window as any).nim = nim;
}
// ---------------------------nim ------------------------------------
const nimSendText = (obj:any) => {
   nim.sendText({
        scene: 'p2p',
        to: obj.to,
        text: obj.text,
        custom: '{"dialogId":"' + obj.dialogId + '"}',
        done: onSendMsgDone,
        needMsgReceipt: obj.needMsgReceipt || false
      })
}

const nimSendCustomMsg = (obj:any) => {
  nim.sendCustomMsg({
      scene: obj.scene,
      to: obj.to,
      pushContent: obj.pushContent,
      content: JSON.stringify(obj.content),
      done: onSendMsgDone
    })
}

const onSendMsgDone = (error:any, msg:any) => {
  console.log(error ? '' : '=>', error || msg)
  if (error) {
    // 
    if (error.code === 7101) {
      msg.status = 'success'
      Message({
        message: error.message,
        type: 'warning'
      });
    } else {
      Message({
        message: error.message,
        type: 'warning'
      });
    }
    return
  }
  ImModule.PutMsg(msg)
}

// 
export const sendMsg = (obj:any) => {
  obj = obj || {}
  let type = obj.type || ''
  switch (type) {
    case 'text':
      nimSendText(obj)
      break
    case 'custom':
      nimSendCustomMsg(obj)
          break
    case 'image':
      nimSendFileMsg(obj)
  }
}

export const nimSendFileMsg = (obj: any) => {
  nim.sendFile({
    scene: 'p2p',
    to: obj.to,
    type: obj.type,
    fileInput: obj.fileInput,
    fastPass: '{"w":200,"h":110}',
    custom: '{"dialogId":"' + obj.dialogId + '"}',
    beginupload: function(upload: any) {
      // -  fileInput,  fileInput
      // - ,  `upload`,  `upload.abort();` 
    },
    uploadprogress: function(obj: any) {
      console.log(': ' + obj.total + 'bytes');
      console.log(': ' + obj.loaded + 'bytes');
      console.log(': ' + obj.percentage);
      console.log(': ' + obj.percentageText);
    },
    uploaddone: function(error: any, file: any) {
      console.log(error);
      console.log(file);
      console.log('' + (!error ? '' : ''));
    },
    beforesend: function(msg: any) {
      console.log('p2p image, id=' + msg.idClient);
      // pushMsg(msg);
    },
    done: onSendMsgDone
  });
}
